.card {
  position: relative;
  cursor: pointer;
}

.card:hover .card__img {
  animation: opacity 0.2s ease-in;
}

@keyframes opacity {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}

.card__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.card__img {
  margin-bottom: 28px;
  border-radius: 20px;
}

.card__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 10px;
}

.card__title {
  font-weight: 500;
  font-size: 32px;
  line-height: 1.56;

  letter-spacing: -0.04em;

  color: #000000;
}
.card__muted {
  font-weight: 500;
  font-size: 24px;
  line-height: 2.08;

  letter-spacing: -0.04em;

  color: #7f7f7f;
}
.card__icon {
  position: relative;
}

.card:hover .card__icon {
  animation: slide 0.5s ease-in;
}

@keyframes slide {
  0% {
    left: 0;
  }
  50% {
    left: -20px;
  }
  100% {
    left: 0;
  }
}
